import store from '@/store';

/**
 * @param {Array} value
 * @returns {Boolean}
 * @example see @/views/permission/Directive.vue
 */
export function checkPermission(value) {
    if (value && value instanceof Array && value.length > 0) {
        const permissions = store.getters && store.getters.currentUserPermissions;
        const requiredPermissions = value;

        const hasPermission = permissions.some(permission => {
            return requiredPermissions.includes(permission);
        });

        return hasPermission;
    } else {
        console.error(`Need permissions! Like v-permission="['manage permission','edit article']"`);
        return false;
    }
}


export function validateSize(file, name, renderer){
    if( file ){
        if( file[0]){
            file.map( (fl, index) => {
                if( fl.size > this.maxSize ){
                    this.errors[name] = ['file size must be less than 2mb'];
                    this[name] = []
                }
                
            })
        } else {
            if( file.size > this.maxSize ){
                this.errors[name] = ['file size must be less than 2mb'];
                this[name] = null;
              }
              else {
                renderer.inputImageRenderer();
              }
        }
       
    }
}

